.total{
    width:20%;
    padding:1.4rem;
    border-radius: 20px;
}
.content{
    background-color: rgb(209, 165, 221);
    padding: 1rem;
    border-radius: 20px;
    height: 100%;
}
.top img{
    height: 100%;
    width: 100%;
    border-radius: 20px;
    max-height: 10rem;
}

.bottom{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 1rem;
}

.total-content{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: baseline;
    gap: 1rem;
}

.delete{
    text-align: center;
    padding: 1rem;
}
.delete button{
    border-radius: 10px;
    height: 2rem;
    cursor: pointer;
}
.form-data{
    text-align: center;
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    padding: 2rem;
    background-color: rgb(79, 59, 98);
    color: white;
    border-radius: 20px;
}

.form-data input{
    margin-left: 1rem;
    border-radius: 1rem;
    height: 1.4rem;
}

.sub{
    cursor: pointer;
}

.new{
   text-align: center;
}

.links{
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}